#headerSection{
  padding: 120px 100px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-height: 1440px;
  margin: 0 auto;
}
#intro_hello{
  font-weight: 300px
}
#intro_hello, #intro_tag{
  font-size: 2.25rem
}
h1{
  display: block;
  margin-block-start: 0.67em;
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}
#intro_tag{
  font-weight: 300;
  max-width: 700px;
}
h2 {
    display: block;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
#my_name{
  font-weight: 700;
}
#intro_contact{
  font-size: 1.25rem;
  font-weight: 400;
}
h3 {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
#highlight_contact{
    transition: all .2s ease-in-out;
    font-weight: 400;
    display: inline-block;
    padding: 2px 3px;
    text-decoration: none;
    box-shadow: inset 0 -3px 0 #007bff;
    color: black;
}


section{
  padding: 50px;
  display: flex;
  justify-content: center;
  padding: 100px 170px;
}
div{
  display: block;
}


.section_title{
  width: 150px;
  margin-right: 50px;
  flex-shrink: 0;
  width: 200px;
  margin-right: 70px;
  color: #007bff;
  text-transform: uppercase;
  text-align: right;
  font-weight: 700;
  letter-spacing: 2px;
}
.section_content{
  font-weight: 300;
  font-size: 1rem;
  width: 100%;
  max-width: 650px;
}
.section__content p {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 1rem;
}
p {
    display: block;
    margin-block-start: 1em;
    margin-block-end: 1em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
}
.section__content .underline-link {
    position: relative;
    display: inline-block;
}
a {
    transition: all .2s ease-in-out;
    text-decoration: none;
    font-weight: 700;
    color: inherit;
}


#skills_table{
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
}
li {
    display: list-item;
    text-align: -webkit-match-parent;
}
ul {
    list-style: none;
    padding: 0;
    margin: 0;
}
.skills_category_header{
  font-weight: 700;
  color: #36363c;
  text-transform: uppercase;

}
.skills_category_item{
  margin: 3px 0;
  font-size: .9rem;
}


#job_list{
  margin-bottom: 100px;
}
.job{
  margin-bottom: 25px;
}
.job .company_time{
  display: -ms-flexbox;
  display: flex;
  justify-content: space-between;
}
.company{
  font-weight: 700;
}
.time{
  font-size: .9rem;
  font-family: Inconsolata,monospace;
}
.job_title{
}
#resume_link{
  font-size: 1.25rem;
  transition: all .2s ease-in-out;
  font-weight: 700;
  display: inline-block;
  background-color: transparent;
  white-space: nowrap;
}


.highlighted_project{
  width: 105%;
  display: flex;
  margin-bottom: 100px;
  margin-left: -5%;
}
.highlighted_project_pic{
  max-width: 700px;
  overflow: visible;
  margin: 0;
  display: block;
}
figure {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 40px;
  margin-inline-end: 40px;
}
.highlighted_project_caption{
  width: 125px;
  flex-shrink: 0;
  margin-left: 30px;
}
.highlighted_project_name{
  margin-bottom: 10px;
}
.highlighted_project p {
    font-size: .85rem;
    line-height: 1.3;
}


.project{
  max-width: 600px;
  margin-bottom: 50px;
}
.project_name{
  margin-bottom: 10px;
}
.project p {
  font-size: .9rem;
  margin-bottom: 20px;
}
.project_tag:first-of-type {
  margin-left: 0;
}
.project_tag {
  display: inline-block;
  margin: 3px 3px 3px 0;
  font-family: Inconsolata,monospace;
  font-size: .75rem;
  font-weight: 700;
  padding: 2px 7px;
  border-radius: 3px;
  color: #007bff;
  border: 1px solid #007bff;
  background: 0 0;
}


.footer{
  padding: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#footer_cc{
    font-weight: 700;
    width: 200px;
}
.footer__links a {
    margin: 0 10px;
}

#footer__links a {
  position: relative;
  display: inline-block;
  color: #007bff!important;
  text-transform: uppercase;
  font-weight: 700;
  outline: 0;
}


